    width: 100%;
    height: auto;
    object-fit: cover;
  }

@media only screen and (max-width: 900px) {
    .cardSizeSmall {
      width: calc(50% - 1em);  /* 2 projects per row */
    }
  }
  
  @media only screen and (max-width: 600px) {
    .cardSizeSmall {
      width: 100%;  /* 1 project per row */
    }
  }
  @media only screen and (max-width: 480px) {
    .cardSizeSmall {
      width: 100%;
      height: auto;
      margin: 1em 0;
    }
  
    .full {
      width: 100%;
      height: auto;
    }
  
    .overlay {
      background-color: grey;
